<template>
     <div class="container">
            <div class="row justify-content-center">
                <div class="col-12 col-md-4">
                    <div class="my-4">
                        <h3 class="text-center text-white font-weight-bold">To Do List ‚ù§</h3>
                    </div>

                    <AddSection @createList="create"></AddSection>

                    <div class="mb-4 d-flex justify-content-between align-items-center">
                        <p class="h3 font-weight-bold text-primary">List Item{{lists.length>1 ? "s" :""}}</p>
                        <p v-if="lists.length>0 && lists.length === totalDone" class="badge badge-success badge-pill mb-0 py-2">all done <i class="fas fa-check-circle"></i> </p>
                        <p v-else class="badge badge-primary badge-pill mb-0 py-2">done {{totalDone}}</p>
                    </div>

          
                    <div class="gv">
                        <ul class="list-group shadow cus-li">
                            <li v-if="lists.length === 0" class="list-group-item text-center">
                                There is no job ü•∫
                            </li>
                            <List1 v-for="list in lists" :key="list" v-else :list="list" @del="del"></List1>
                        </ul>
                    </div>

                </div>
            </div>
        </div>

</template>

<script>

import AddSection from './components/AddSection'
import List1 from './components/List1'
export default {
  components: {
    AddSection,
    List1, 
     },
          name :"App",
          
           data() {
             return {
                currentId : 0,
                lists :[]
             }
           },

            computed : {
                totalDone (){
                    return this.lists.filter(el =>el.isDone === true).length
                }
            },

            methods : {
                create (x){
                    this.currentId++;
                    this.lists.push({
                        id : this.currentId,
                        message : x,
                        isDone : false,
                    });
                    this.newMessage = "";
                },
                del (x){
                     setTimeout(() => this.lists = this.lists.filter(el => el.id !== x),600)               
                },
                
            },
  }
</script>

