<template>
         <li  :class="['list-group-item d-flex justify-content-between align-items-center showeffect',{'dele':isDelect}]">

             <input v-if="isEdit" @keyup.enter="isEdit =false" type="text" v-model="list.message" class="custom-control-lg mr-2">

             <div v-else class="custom-control custom-checkbox">
                 <input v-model="list.isDone" type="checkbox"  class="custom-control-input" :id="'customCheck'+list.id">
                 <label :for="'customCheck'+list.id" :class="['custom-control-label',{'done':list.isDone}]">{{list.message}}</label>
             </div>
             <div class="">
                 <button @click="isEdit=true" @dblclick ="list.isEdit=false" class="btn btn-primary mr-2"><i class="fas fa-pen"></i></button>
                 <button @click="[isDelect = true,del(list.id)]" class="btn btn-danger"><i class="fas fa-trash"></i></button>
             </div>
           
         </li>
</template>

<script>
    export default {
        name : "List1",
        data() {
            return {
                isEdit : false,
                isDelect:false,
            }
        },
        props: {
            list: {
                type: Object,
                required : true,
            },
        },
        methods: {
            del(x) {
                this.$emit("del",x)
            }
        },
    }
</script>

