<template>
   
         <div class="form-row mb-4">
             <div class="col-10">
                    <input @keyup.enter='create()' @keyup="errorMessage=false" v-model="newMessage" type="text" class="form-control" required>
             </div>
             <div class="col-2">
                 <button @click="create()" class="btn btn-primary text-white shadow"><i class="fas fa-plus"></i></button>
             </div>

             <div class="col-12"><small v-if="errorMessage" class="text-danger font-weigth-bold">Do something Don't be lazy</small></div>
         </div>
        
</template>

<script>
    export default {
        name : "AddSection",

        data() {
            return {
                newMessage : "",
                errorMessage : false,
            }
        },
        methods: {
            create() {
                if(this.newMessage.length){
                    this.$emit("createList",this.newMessage);
                    this.newMessage = "";
                }else{
                    this.errorMessage = true;
                }
            }
        },
    }
</script>

